* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html {
  font-family: 'Helvetica', 'Arial', sans-serif;
  font-size: 18px;
}

body {
  padding: 30px 0;
  background: hsl(0, 0%, 96%);
}

#app {
  width: 300px;
  height: 500px;
  background: hsl(0, 0%, 100%);
  -webkit-box-shadow: 3px 3px 8px hsl(0, 0%, 81%);
          box-shadow: 3px 3px 8px hsl(0, 0%, 81%);

  margin: 0 auto;
}


